/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 768px) {
  /* Style the navigation menu */
  .topnav {
    position: absolute;
    right: 70px;
    top: 10px;
  }

  img {
    width: 15% !important;
  }

  .language {
    margin-right: 10%;
    flex-direction: row;
    display: flex;
    flex-wrap: wrap;
    margin-top: 20px;
    margin: 0 auto;
    justify-content: center;
    & > span {
      font-size: 28px;
      padding: 20px;
      margin: 10px;
    }
  }

  /* Hide the links inside the navigation menu (except for logo/home) */
  .topnav #myLinks {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: #131313;
    z-index: 2;
    padding: 50px 0;
  }

  /* Style navigation menu links */
  .topnav a {
    color: white;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
    display: block;
  }

  /* Style the hamburger menu */
  .topnav a.icon {
    z-index: 1;
    background: #131313;
    display: block;
    position: absolute;
    right: 0;
    top: 0;
    float: right;
  }

  /* Add a grey background color on mouse-over */
  .topnav a:hover {
    background-color: #131313;
    color: black;
  }

  /* Style the active link (or home/logo) */
  .active {
    background-color: #4caf50;
    color: white;
  }

  .faIcon {
    position: absolute;
    font-size: 38px;
    font-size: 38px;
    right: 80px;
    position: absolute;
    top: 10px;
  }

  .bars {
    right: 0px;
    top: 0px;
  }

  .link {
    text-align: center;
    font-size: 48px !important;
  }
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 768px) {
  .mobileNavbar {
    display: none;
  }
}

